@font-face {
  font-family: Epilogue;
  src: local(Epilogue Light), local(Epilogue-Light), url("Epilogue-Light.5f1f0a5f.woff2") format("woff2"), url("Epilogue-Light.e0503ba4.woff") format("woff"), url("Epilogue-Light.f10a0614.ttf") format("truetype");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Epilogue;
  src: local(Epilogue Regular), local(Epilogue-Regular), url("Epilogue-Regular.d60fe198.woff2") format("woff2"), url("Epilogue-Regular.a1c53fad.woff") format("woff"), url("Epilogue-Regular.14d956c1.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Epilogue;
  src: local(Epilogue SemiBold), local(Epilogue-SemiBold), url("Epilogue-SemiBold.a2908476.woff2") format("woff2"), url("Epilogue-SemiBold.1ec8ff40.woff") format("woff"), url("Epilogue-SemiBold.bc58743e.ttf") format("truetype");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

:root {
  --color-text: #fc4308;
  --accent: #fc4308;
  --bg-color: #313e2a;
  --bg-color-2: #0d1e20;
  --bg-color-3: #262d20;
  --white: #fff;
  --black: #000;
  --orange: rgba(252, 67, 8, .1);
  --green-shadow: rgba(43, 51, 36, .827);
  --mob-green-shadow: rgba(13, 30, 32, .2);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--bg-color);
  color: var(--accent);
  font-family: Epilogue, sans-serif;
}

.header {
  width: 100%;
  z-index: 2;
  box-shadow: 1px 5px 25px var(--green-shadow);
  margin: 0 auto;
  padding: 20px;
  display: flex;
  position: absolute;
}

.container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-logo {
  color: var(--color-text);
  padding: 5px 0;
  font-size: 12px;
  line-height: 1.1;
}

.header-logo:hover, .header-logo:active {
  transition: all 1.5s;
  transform: scale(1.3);
}

.header-nav {
  display: none;
}

.mobile-menu-btn {
  width: 24px;
  height: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.mobile-menu-pic {
  width: 24px;
  height: 24px;
  fill: var(--accent);
}

.mobile-menu-pic:hover {
  transition: all 1.5s;
  transform: scale(1.2);
}

.mob-modal {
  z-index: 3;
  width: 100%;
  height: 100vh;
  background-color: var(--bg-color-3);
  box-shadow: -10px -1px 10px var(--green-shadow);
  border-radius: 4px;
  flex-direction: column;
  gap: 150px;
  padding: 20px;
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0%;
  left: 0%;
  overflow: visible;
  transform: translate(0);
}

.mob-top-menu {
  box-shadow: 0 3px 15px var(--mob-green-shadow);
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mob-x-btn {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.mob-x-pic {
  width: 32px;
  height: 32px;
  fill: var(--accent);
}

.mob-x-btn:hover, .mob-x-btn:active {
  transition: all 1.5s;
  transform: scale(1.3);
}

.mob-list {
  flex-direction: column;
  justify-content: center;
  gap: 24px;
  margin: 0 auto;
  display: flex;
}

.mob-item {
  text-transform: uppercase;
  color: var(--color-text);
  padding: 5px 2px;
  font-size: 15px;
  line-height: 1.1;
}

.mob-item:hover, .mob-item:active {
  transition: all 1.5s;
  transform: scale(1.3);
}

.appear {
  transform: translateY(-100%);
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }

  .header-logo {
    font-size: 15px;
  }

  .mobile-menu-btn {
    display: none;
  }

  .header-nav {
    display: flex;
  }

  .header-list {
    gap: 40px;
    display: flex;
  }

  .header-item {
    text-transform: uppercase;
    color: var(--color-text);
    padding: 5px 2px;
    font-size: 15px;
    line-height: 1.1;
  }

  .header-item:hover {
    transition: all 1.5s;
    transform: scale(1.3);
  }

  .header-link {
    padding: 20px 2px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

